
'use client';

import { useState } from 'react';
import { useActionState } from 'react';
import { useFormStatus } from 'react-dom';
import LoginButtons from '@/components/auth/LoginButtons';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Separator } from '@/components/ui/separator';
import { KeyRound, ShieldAlert, Loader2 } from 'lucide-react';
import { handleStaffLogin } from '@/app/actions';

function StaffLoginSubmitButton() {
  const { pending } = useFormStatus();
  return (
    <Button type="submit" className="w-full" disabled={pending}>
      {pending && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
      登录
    </Button>
  );
}

export default function LoginPage() {
  const [showStaffLogin, setShowStaffLogin] = useState(false);
  const [staffLoginState, staffLoginAction, isStaffLoginPending] = useActionState(handleStaffLogin, undefined);


  return (
    <div className="flex items-center justify-center py-12">
      <Card className="w-full max-w-md shadow-xl">
        <CardHeader className="text-center">
          <CardTitle className="text-3xl font-bold text-primary">欢迎回来</CardTitle>
          <CardDescription>
            {showStaffLogin ? 'STAFF 账户登录' : '请选择一个方式登录到 像素建筑馆'}
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-6">
          {!showStaffLogin ? (
            <>
              <LoginButtons />
              <Separator />
              <div className="text-center">
                <Button variant="link" onClick={() => setShowStaffLogin(true)} className="text-sm text-muted-foreground hover:text-primary">
                  使用VRCub STAFF帐户登陆
                </Button>
              </div>
            </>
          ) : (
            <form action={staffLoginAction} className="space-y-4 pt-2">
              <h3 className="text-lg font-semibold text-center text-primary flex items-center justify-center">
                <KeyRound className="mr-2 h-5 w-5" /> STAFF 登录
              </h3>
              <div>
                <label htmlFor="username" className="sr-only">员工帐户</label>
                <Input
                  id="username"
                  name="username"
                  type="text"
                  placeholder="员工帐户"
                  required
                  className="text-center"
                />
              </div>
              <div>
                <label htmlFor="password" className="sr-only">密码</label>
                <Input
                  id="password"
                  name="password"
                  type="password"
                  placeholder="密码"
                  required
                  className="text-center"
                />
              </div>
              {staffLoginState?.error && (
                <Alert variant="destructive" className="text-sm">
                  <ShieldAlert className="h-4 w-4" />
                  <AlertTitle>登录失败</AlertTitle>
                  <AlertDescription>{staffLoginState.error}</AlertDescription>
                </Alert>
              )}
              <StaffLoginSubmitButton />
               <Button variant="link" onClick={() => setShowStaffLogin(false)} className="text-sm text-muted-foreground w-full hover:text-primary">
                返回选择其他登录方式
              </Button>
            </form>
          )}
        </CardContent>
         <CardFooter className="text-xs text-muted-foreground pt-6 border-t mt-2">
          <p className="text-center w-full">
            <strong>安全提示:</strong> STAFF 账户登录功能仅为内部演示使用，
            <br />
            请勿在实际生产环境中使用此方式。
          </p>
        </CardFooter>
      </Card>
    </div>
  );
}
